import React, {Component} from 'react';
import './styles/SideBar.css';
import IsraelUnionLogo from '../imgs/IsraelUnionLogo.png'

class SideBar extends Component {
	constructor({active}){
		super();
		this.state = {  
			active:active
	  	}

	}


	render(){

		const getClass=(number)=>{
			if(number===parseInt(this.state.active))
				return("activesidenavtabs sidenavtabs");
			else
				return("sidenavtabs");
		}


		let pages = [
			<div id="LogoDiv"><a href="./#/Home"><img className="Logo" src={IsraelUnionLogo}/></a></div>,
			<p className={getClass(1)}> <a href="./#/Home"> </a></p>,
		    <p className={getClass(2)}> <a href="./#/WhatIsBoccia">   <br/>'?</a></p>,
		    <p className={getClass(3)}> <a href="./#/HowToPlay">  <br/>?</a></p>,
		    <p className={getClass(4)}> <a href="./#/BocciaClasses"></a></p>,
		    <p className={getClass(5)}> <a href="./#/Rules">   </a></p>,
		    <p className={getClass(6)}> <a href="./#/MeetThePlayers">' <br/></a></p>,
		 	<p className={getClass(7)}> <a href="./#/Contact">   </a></p> 
		]
		

		
		return(
			<div>				
				<div className="sidenavbar">
					{pages}
				</div>		
			</div>

		)	
		

	}
}

export default SideBar;